import { useContext } from "react";
import { ThemeContext } from "@dash/Context/ThemeContext";
import { getStylesForItem, getUUID } from "@dash/Utils";
import { themeObjects } from "@dash/Utils/themeObjects";

/**
 * AlertBanner - A modern, visually distinctive alert component
 *
 * Design Philosophy:
 * - Bold left border accent for immediate visual impact
 * - Color-coded variants with consistent semantics
 * - Clean typography with strong hierarchy
 * - Smooth animations for professional feel
 * - Icons for quick recognition
 *
 * Variants:
 * - info (blue): Informational messages
 * - success (green): Success confirmations
 * - warning (amber): Warning messages
 * - error (red): Error alerts
 */

const VARIANT_STYLES = {
    info: {
        container: "bg-blue-50 border-l-4 border-blue-500",
        title: "text-blue-900",
        message: "text-blue-800",
        icon: "text-blue-600",
        closeButton: "text-blue-600 hover:text-blue-900 hover:bg-blue-100",
    },
    success: {
        container: "bg-green-50 border-l-4 border-green-500",
        title: "text-green-900",
        message: "text-green-800",
        icon: "text-green-600",
        closeButton: "text-green-600 hover:text-green-900 hover:bg-green-100",
    },
    warning: {
        container: "bg-amber-50 border-l-4 border-amber-500",
        title: "text-amber-900",
        message: "text-amber-800",
        icon: "text-amber-600",
        closeButton: "text-amber-600 hover:text-amber-900 hover:bg-amber-100",
    },
    error: {
        container: "bg-red-50 border-l-4 border-red-500",
        title: "text-red-900",
        message: "text-red-800",
        icon: "text-red-600",
        closeButton: "text-red-600 hover:text-red-900 hover:bg-red-100",
    },
};

// SVG Icons for each variant
const Icons = {
    info: (
        <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
        >
            <path
                fillRule="evenodd"
                d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z"
                clipRule="evenodd"
            />
        </svg>
    ),
    success: (
        <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
        >
            <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                clipRule="evenodd"
            />
        </svg>
    ),
    warning: (
        <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
        >
            <path
                fillRule="evenodd"
                d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                clipRule="evenodd"
            />
        </svg>
    ),
    error: (
        <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
        >
            <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
                clipRule="evenodd"
            />
        </svg>
    ),
};

const AlertBanner = ({
    variant = "info", // info | success | warning | error
    title = "",
    message = "",
    children = null,
    onClose = null,
    showIcon = true,
    className = "",
    animate = true,
    ...props
}) => {
    const { currentTheme } = useContext(ThemeContext);

    // Get theme-aware styles while allowing variant overrides
    const styles = getStylesForItem(themeObjects.ALERT_BANNER, currentTheme, {
        ...props,
    });

    const uuid = getUUID("", "alert-banner");
    const variantStyle = VARIANT_STYLES[variant] || VARIANT_STYLES.info;
    const icon = Icons[variant] || Icons.info;

    // Animation classes
    const animationClass = animate
        ? "animate-in slide-in-from-top-2 fade-in duration-300"
        : "";

    return (
        <div
            id={uuid}
            className={`
                ${variantStyle.container}
                rounded-md p-4 shadow-sm
                ${animationClass}
                ${className}
            `}
            role="alert"
            aria-live="polite"
            aria-atomic="true"
        >
            <div className="flex items-start gap-3">
                {/* Icon */}
                {showIcon && (
                    <div
                        className={`
                            ${variantStyle.icon}
                            flex-shrink-0
                            mt-0.5
                        `}
                        aria-hidden="true"
                    >
                        {icon}
                    </div>
                )}

                {/* Content */}
                <div className="flex-1 min-w-0">
                    {title && (
                        <h3
                            className={`
                                ${variantStyle.title}
                                text-sm font-bold mb-1
                            `}
                        >
                            {title}
                        </h3>
                    )}
                    {message && (
                        <p
                            className={`
                                ${variantStyle.message}
                                text-sm leading-relaxed
                            `}
                        >
                            {message}
                        </p>
                    )}
                    {children && (
                        <div
                            className={`
                                ${variantStyle.message}
                                text-sm
                                ${title || message ? "mt-2" : ""}
                            `}
                        >
                            {children}
                        </div>
                    )}
                </div>

                {/* Close Button */}
                {onClose && (
                    <button
                        type="button"
                        onClick={onClose}
                        className={`
                            ${variantStyle.closeButton}
                            flex-shrink-0
                            rounded-md p-1.5
                            transition-colors duration-200
                            focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-transparent
                        `}
                        aria-label="Dismiss alert"
                    >
                        <svg
                            className="w-4 h-4"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                fillRule="evenodd"
                                d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                clipRule="evenodd"
                            />
                        </svg>
                    </button>
                )}
            </div>
        </div>
    );
};

export { AlertBanner };
